import React, {Component} from "react";
import './App.css';

function App() {
  return <Tablet/>
}

class Tablet extends Component {

    constructor() {
        super();
        this.state = {
            left: 1040,
            top: 488
        };
    }

    randomNumber(max){
        return Math.floor(Math.random() * max);
    }

    no =()=> {
        this.setState({
            left: this.randomNumber(window.screen.width),
            top: this.randomNumber(window.screen.height)
        });
    }

    noClicked() {alert("Мы подумаем над повышением зарплаты")}

    yes() {alert("Спасибо за отзыв!")}

    render(){

        const { top, left } = this.state;

        return(
            <div className="background">
                <p id="question">Вас устраивает ваша зарплата?</p>
                <button
                    id="yes"
                    onClick={this.yes}
                >
                    Да
                </button>
                <button
                    id="no"
                    onMouseEnter={this.no}
                    onClick={this.noClicked}
                    style={{
                        top,
                        left
                    }}
                >
                    Нет
                </button>
            </div>
        )
    }
}

export default App;
